.title {
  margin-bottom: 48px;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 700;
  text-align: center;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
  color: $text-color-black;

  &--left {
    text-align: left;
    line-height: 48px;
    margin-bottom: 32px;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop {
      grid-column: 1 / 6;
    }
  }

  @include on-tablet {
    font-size: 40px;
    margin-bottom: 64px;
  }

  @include on-desktop {
    font-size: 48px;
  }
}
